<template>
  <div class="home-section">
    <header class="header-section"></header>
    <ul class="menu-section">
	<li class="menu-item">
	    <img class="menu-img ticket-icon"/>
	    <p class="menu-name">火车机票</p>
	</li>
	<li class="menu-item">
	    <img class="menu-img waimai-icon"/>
	    <p class="menu-name">外卖</p>
	</li>
	<li class="menu-item" @click="toMenu(3)">
	    <img class="menu-img movie-icon"/>
	    <p class="menu-name">电影</p>
	</li>
	<li class="menu-item" @click="toMenu(4)">
	    <img class="menu-img music-icon"/>
	    <p class="menu-name">音乐</p>
	</li>
    </ul>
    
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      msg: ''
    }
  },
  methods : {
    toMenu(type){
	type == 3 && this.$router.push('/movie/');
	type == 4 && this.$router.push('/music/');
    }
  }
}
</script>

<style scoped rel='stylesheet/scss' lang='scss' >
@import '../assets/css/mixin.scss';
.home-section{
    .header-section{
	height:toRem(40px);
	background:#eee;
    }
    .menu-section{
	display:flex;
	align-items:center;
	padding: toRem(10px);
	.menu-item{
	    width: toRem(64px);
	    text-align: center;
	    &:active{
		opacity:0.8;
	    }
	    .menu-img{display:inline-block;
		width:toRem(36px);height:toRem(36px);
		background:#f5f5f5;

	    }
	    .menu-name{
		line-height:toRem(20px);
		text-align:center;
		font-size:toRem(12px);
	    }
	}
	
    }
}

</style>



