const TopNav = r => require.ensure([], () => r(require('@/components/topNav/topNav')), 'TopNav');



export default {
  data(){
      return {
	      contactShow : false,
	  }
  },
  mounted : function(){
	 
  },
  methods: {
      
	  baiduTongJi(){
		if(window.baidutongji)return;
		var _hmt = _hmt || [];
		(function() {
		  window.baidutongji = true;
		  var hm = document.createElement("script");
		  hm.src = "https://hm.baidu.com/hm.js?3f929a955d65863db7ef234ff7ebff09";
		  var s = document.getElementsByTagName("script")[0]; 
		  s.parentNode.insertBefore(hm, s);
		  
		})();
	  },
	  
  },
  components:{
	  TopNav
  },
  beforeDestroy(){
	  
  }
}


