<template>
  <div class="movie-section">
    <TopNav title="电影"/>
    <div class="movie-type-warper" ref='movieTypeList'>
	    <ul class="movie-type-list" >
		<li class="movie-type-item active">精选</li>
		<li class="movie-type-item">电视剧</li>
		<li class="movie-type-item">电影</li>
		<li class="movie-type-item">动画片</li>
		<li class="movie-type-item">综艺</li>
		<li class="movie-type-item">动漫</li>
		<li class="movie-type-item">少儿</li>
		<li class="movie-type-item">记录片</li>
		<li class="movie-type-item">NBA</li>
		<li class="movie-type-item">爱玩</li>
		<li class="movie-type-item">游戏</li>
	    </ul>
    </div>
    <div class="">
	<ul class="movie-result-list" >
		<li class="movie-result-item active" @click="toMovieDetail(1,100)">精选</li>
		<li class="movie-result-item">电视剧</li>
		<li class="movie-result-item">电影</li>
		<li class="movie-result-item">动画片</li>
		<li class="movie-result-item">综艺</li>
		<li class="movie-result-item">动漫</li>
		<li class="movie-result-item">少儿</li>
		<li class="movie-result-item">记录片</li>
		<li class="movie-result-item">NBA</li>
		<li class="movie-result-item">爱玩</li>
		<li class="movie-result-item">游戏</li>
	</ul>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import clientServerMixin from "mixins/clientServerMixin.js";
import BScroll from 'better-scroll'

export default {
  mixins : [clientServerMixin],
  name: 'MovieIndex',
  data () {
    return {
      msg: '',
      scroll : null,
    }
  },
  mounted (){
    var _this = this;
    this.scroll = new BScroll(this.$refs.movieTypeList, {
	click: true,
	scrollX : true,
	scrollY : false,
    })
     this.scroll.on("scrollEnd", ()=>{
	// 刷新 dom刷新不及时的情况
	_this.scroll.refresh();
	
    })
  },
  methods : {
     toMovieDetail(number, id){
	number == 1 && this.$router.push('/movie/detail');
     }
  }
}
</script>

<style scoped rel='stylesheet/scss' lang='scss' >
@import '../../assets/css/mixin.scss';
.movie-section{
    .movie-type-warper{
	overflow:hidden;
	    .movie-type-list{
		height:toRem(30px);
		display: flex;
		align-items: center;
		padding: toRem(10px);
		flex-wrap: nowrap;
		width: max-content;
		.movie-type-item{
		    display:inline-block;
		    font-size:toRem(12px);
		    white-space: nowrap;
		    margin: 0 toRem(10px);
		    &.active{
			font-size:toRem(16px);
			color:#e08528
		    }
		}
	    }
    }
    .movie-result-list{
	.movie-result-item{
		height:60px;
		border-top:1px solid #eee;
		font-size:toRem(18px);
	}
    }
}

</style>



